/* src/FolderView.css */
.folder-view {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: white;
  }
  
  .topbar {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background-color: #f8f8f8;
    border-bottom: 1px solid #ccc;
  }
  
  .topbar-button {
    background-color: transparent;
    border: none;
    font-size: 16px;
    cursor: pointer;
    margin: 0 10px;
    padding: 10px 20px;
    border-radius: 5px;
    transition: background-color 0.3s;
    text-decoration: none;
    color: inherit;
  }
  
  .topbar-button:hover {
    background-color: #e0e0e0;
  }
  
  .content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }
  
  .content h1 {
    font-size: 36px;
    color: black;
  }
  
  @media (min-width: 768px) {
    .topbar {
      justify-content: flex-start;
      padding: 10px 20px;
    }
  
    .topbar-button {
      font-size: 18px;
      margin: 0 15px;
    }
  
    .content h1 {
      font-size: 48px;
    }
  }
  